import * as React from 'react';
// @ts-ignore
import ring from '../../assets/images/ring.png';
// @ts-ignore
import ringDrag from '../../assets/images/ring_draggable.png';
import './styles.css';
import { DragSource } from 'react-dnd';

interface Props {
  reportMiss: () => void;
  canToss: boolean;
  connectDragSource: (children: JSX.Element) => React.ReactNode;
}

const collect = (connect: any, monitor: any) => {
  return {
    connectDragSource: connect.dragSource(),
  };
};

const ringSource = {
  beginDrag(props: any) {
    return {};
  }
};

export class RingComponent extends React.Component<Props> {
  render() {
    if (!this.props.canToss) {
      return null;
    }

    return this.props.connectDragSource(
      <div
        className="ring"
      >
        <img
          className="ring__wrapper"
          src={ring}
          alt="toss ring"
        />
      </div>
    );
  }

  handleDrag = (event: React.DragEvent<HTMLElement>) => {
    const img = new Image(); 
    img.src = ringDrag;
    event.dataTransfer.dropEffect = 'link'; 
    event.dataTransfer.setDragImage(img, 25, 25);
  }

  handleDragEnd = (event: React.DragEvent<HTMLElement>) => {
    if (event.dataTransfer.dropEffect === 'none') {
      this.props.reportMiss();
    }
  }
}

export const Ring = DragSource('Ring', ringSource, collect)(RingComponent);



