import * as React from 'react';
import './styles.css';
import { DropTarget } from 'react-dnd';

interface Props {
  reportMiss: () => void;
  connectDropTarget: (children: JSX.Element) => React.ReactNode;
}

const missWrapperTarget = {
  drop(props: any, monitor: any) {
    props.reportMiss();
  },
};

function collect(connect: any, monitor: any) {
  return {
    connectDropTarget: connect.dropTarget(),
  };
}

export class MissWrapperComponent extends React.Component<Props> {
  public render() {
    return this.props.connectDropTarget(
      <div className="miss-wrapper" />
    );
  }
}

export const MissWrapper = DropTarget('Ring', missWrapperTarget, collect)(MissWrapperComponent);



