import * as React from 'react';
import './styles.css';
import { DropTarget } from 'react-dnd';

interface Props {
  imgSrc: string;
  prizeType: string;
  prizeID: string;
  onPrizeHit: (prizeType: string) => void;
  connectDropTarget: (children: JSX.Element) => React.ReactNode;
  isOver: boolean;
  canDrop: boolean;
}

const prizeTarget = {
  drop(props: any, monitor: any) {
    const prizeID = props.prizeID;
    props.onPrizeHit(prizeID);
  },
};

function collect(connect: any, monitor: any) {
  return {
    connectDropTarget: connect.dropTarget()
  };
}

class PrizeComponent extends React.Component<Props> {
  render() {
    return this.props.connectDropTarget(
      <div>
        <img
          className="prize"
          src={this.props.imgSrc}
          alt="prize"
        />
      </div>
    );
  }
}

export const Prize = DropTarget('Ring', prizeTarget, collect)(PrizeComponent);



